.bm-cross-button.my-cross {
  transform: scale(1.2);
  margin-top: 1vw;
  transition: 0.2s;
  margin-right: 85vw;
  z-index: 1000;
}

#menu-links {
  display: flex;
  justify-content: space-between;
  gap: clamp(24px, 12vw, 32px);
  align-items: flex-start;
  flex-direction: column;
  margin-top: #{"max(7vw, 100px)"};
}

#menu-contents {
  display: flex;
  padding-right: 2vw;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  height: 100%;
}

.menu-link {
  transition: 1s;
  color: white;
  font-family: iran-yekan, serif;
  font-size: clamp(12px, 1.3vw, 50px);
}

.menu-link:hover {
  color: #4286f5;
}

#company-name {
  margin-top: auto;
  margin-right: 0;
  color: white;
  font-family: iran-yekan, serif;
  font-size: 0.8vw;
  text-align: right;
}

.my-button {
  transition: 0.4s;
  position: absolute;
  margin-top: 2.2vw;
  margin-right: 88vw;
  width: 3vw;
}

.my-button:active,
.my-cross:active {
  transform: scale(0.8);
}

.menu {
  z-index: 5;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #0a0a0a;
  position: absolute;
  margin-right: 84.5vw;
  width: 20vw;
}

@media screen and (max-width: 600px) {
  #company-name {
    display: none;
  }

  #menu-links {
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    height: 40vh;
  }

  #menu-contents {
    padding-right: 0;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .menu-link {
    font-size: 6.5vw;
  }

  .my-button {
    display: none;
  }

  .menu {
    display: none;
  }
}
